.app{

}

.menu{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 100vh;
    & > div{
        cursor: pointer;
        position: relative;
        transition: .1s;
        overflow: hidden;
        &:hover h2{
            color: #fff;
            transform: scale(1.07) ;
        }
    }
    h2{
        width: 100%;
       
        text-align: center;
        line-height: 100vh;
        margin: 0;
        transition: .1s;
        font-size: 22px;
        
    }
}

.upload{
    background: rgb(255, 93, 241);
    &:hover{
        background: rgb(255, 0, 234);
    }
    button{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
    }
    input{
        width: 100%;
        height: 100%;
    }
    
}

.calculator{
   
    background: rgb(0, 119, 255);
    &:hover{
        background: rgb(37, 70, 255);
    }
}

.table{
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 10px;
    margin-top: 20px;
    padding: 20px;
}

.row{
    display: grid;

    grid-template-columns: 20px 0.5fr 2fr 70px 30px 100px 100px 200px;
    column-gap: 5px;
    border: 1px solid rgb(92, 92, 92);
    padding: 10px 5px;
    transition: .1s;

    &:hover{
        transform: scale(1.01);
        border-color: rgb(255, 83, 83);
    }
    &:first-child{
        font-size: 12px;
        &:hover{
            border-color: rgb(92, 92, 92);
            transform: scale(1);
        }
       
    }
    
}

.index{
    border: 1px solid red;
    text-align: center;
}